body {
	color: #333; 
	background-color:#000;
	font-family: 'Inconsolata', sans-serif ;
}

#beat {
	position:absolute;
	width:100%;
	height:100%;
	-webkit-animation: pulsate 1s ease-out;
	-webkit-animation-iteration-count: infinite; 
	opacity: 1.0;
}

@-webkit-keyframes pulsate {
	0% {opacity: 0.9;}
	50% {opacity: 1.0;}
	100% {opacity: 0.9;}
}


header {
	
}

#beatNow {
	margin:0px auto;
	font-size:60px;
	color:#fff;
	line-height:0.7em;
	height:200px;
	-webkit-animation: fadein 3s ease-in;
	opacity:0.1;
}

@-webkit-keyframes fadein {
	0% {opacity: 0.0;}
	50% {opacity: 0.05;}
	100% {opacity: 0.1;}
}

#beatMinute {
	color:#fff;
	margin-left:-8px;
	font-size: 250px;
	opacity:0;
	-webkit-animation: fontbulger 1s ease-out;
	-webkit-animation-iteration-count: infinite;
}

@-webkit-keyframes fontbulger {
  0%, 100% {
    opacity: 0.3;
  }
  50% {
    opacity:0.2;
  }
}


#thisBeat {
	color:#000;
	height:300px;
}

// form styles

input {
	display: block;
	width: 100%;
	height: 50px;
	padding: 6px 9pt;
	font-size: 30px;
	font-weight:100;
	line-height: 1.428571429;
	color: #000;
	background:url(../images/trans.gif);
	background-color: none !important;
	background-image: none;
	border: none;
	border-radius: none;
	-webkit-box-shadow: none;
	box-shadow: none;
	-webkit-transition: none;
	-o-transition: none;
	transition: none;
	
}

*:focus {
	background-color: none !important;
	border: none;
	outline: none;
}
.form-beat {
	background-color: none !important;
}

:-moz-placeholder {
    color: #000;
}

::-webkit-input-placeholder {
    color: #000;
}








.gps_ring {
	border: 3px solid #fff;
	-webkit-border-radius: 30px;
